@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fe99a9;
  background: linear-gradient(90deg, rgba(67,222,188,1) 0%, rgba(150,249,226,1) 100%);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
  background-color: #77F249;
}


.App {
  text-align: center;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom:0;
  z-index: -1;
}
.Tilt {
    background: radial-gradient(circle, rgba(217,254,203,1) 0%, rgba(150,249,226,1) 100%)
    ;}
.form {
    width: 800px;
    background-color: #77F249;
    background-image: linear-gradient(30deg, #43debc 12%, transparent 12.5%, transparent 87%, #43debc 87.5%, #43debc),
    linear-gradient(150deg, #43debc 12%, transparent 12.5%, transparent 87%, #43debc 87.5%, #43debc),
    linear-gradient(30deg, #43debc 12%, transparent 12.5%, transparent 87%, #43debc 87.5%, #43debc),
    linear-gradient(150deg, #43debc 12%, transparent 12.5%, transparent 87%, #43debc 87.5%, #43debc),
    linear-gradient(60deg, #96f9e2 25%, transparent 25.5%, transparent 75%, #96f9e2 75%, #96f9e2),
    linear-gradient(60deg, #96f9e2 25%, transparent 25.5%, transparent 75%, #96f9e2 75%, #96f9e2);
    background-size:80px 140px;
    background-position: 0 0, 0 0, 40px 70px, 40px 70px, 0 0, 40px 70px;
}

.texting {
    text-align: center;
}
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #77F249 inset;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    cursor: pointer;
}
